* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-image: linear-gradient(120deg, #4e4e4e 0%, #d84921 100%);
  color: white;
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
}
header {
  font-size: 2rem;
}

header,
form {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
form input,
form button {
  padding: 0.5rem;
  font-size: 2rem;
  border: none;
  background: white;
}
form button {
  color: #ff6f47;
  background: #f7fffe;
  cursor: pointer;
  transition: all 0.3s ease;
}
form button:hover {
  background: #ff6f47;
  color: white;
}
.todo-container {
  display: flex;
  justify-content: center;
  align-items: center;
 
}

.todo-list {
  min-width: 30%;
  list-style: none;
}
.todo-date{
  margin-right: 20px;
 
}
#date-label{
  color: red;
  padding : .3em .5em;
  border-right: 3px solid black;
}

.todo {
  margin: 0.5rem;
  background: white;
  width:800px;
  font-size: 1.5rem;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 1s ease;
}
.filter-todo {
  padding: 1.1rem;
}
.task-label{
  font-size: 2rem;
  padding: 10px;
  margin-left:20px ;
}
.todo li {
  flex: 1;
 
  
}

.trash-btn,
.complete-btn {
  background: #ff6f47;
  color: white;
  border: none; 
   padding : 2rem 1em;
  
  cursor: pointer;
  font-size: 1rem;
}
.trash-btn:hover{
  background-color: red;
}
.complete-btn {
  background: rgb(11, 212, 162);
  border-radius: right 10px;
}
.complete-btn:hover{
  background:green;
}

.todo-item {
  padding: 0rem .5rem;
}
.date-item {
  padding: 0rem .5rem;
  margin-right: 5px;
}

.fa-trash,
.fa-check {
  pointer-events: none;
}

.fall {
  transform: translateY(10rem) rotateZ(20deg);
  opacity: 0;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

/*CUSTOM SELECTOR */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background-image: none;
}

/* Custom Select */
.select {
  margin: 1rem;
  position: relative;
  overflow: hidden;
 
}
select {
  color: #f15226;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  width: 12rem;
}
/* Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  background: #df451a;
  cursor: pointer;
  pointer-events: none;
}
